@import url(https://fonts.googleapis.com/css2?family=Merienda&family=Overlock+SC&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Overlock SC', cursive !important;
  font-size: 18px;
  
  
}

button {
  padding: 16px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: black;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

h3, h4 {
  margin-bottom: 0;
}

nav {
  width: 100%;
  background-color: rgba(0, 0, 0, .5 );
  position: fixed;
  top: 0;
  z-index: 100;
}
.App {
  text-align: center;

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
#nav-space{
  max-height: 70px;

  max-width: 1000px;
  margin: auto;

  display: flex;
}

#nav-logo img{
  max-width: 120px;

  flex: 1 1;
}

#nav-links{
  border-bottom: 4px solid black;
  vertical-align: middle;
  text-align: center;
  flex: 1 1;
}

#nav-links ul{
  align-content: center;
}
nav ul {
  
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 27px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  background: black;
  color: white;
}

#page-body {
  max-width: 800px;
  margin: auto;
  margin-top: 66px;
}

.carousel {
  width:100%;
  max-height: 500px;
  margin: auto;
  align-items: center;
}

.carousel-control-next, .carousel-control-prev  {
  position: absolute;
  top: 50% !important;
  bottom: 0;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 10% !important;
  height: 10% !important;
  border-radius: 100%;
  padding: 0;
  color: #ba0000;
  text-align: center;
  background: 0 0;
  background-color: white!important;
  border: 0;
  opacity: .3;
  transition: opacity .15s ease;
}

.carousel-item{

  display: flex;
  float: left;
  width: 100%;
  margin-right: -100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transition: -webkit-transform .6s ease-in-out;
  transition: transform .6s ease-in-out;
  transition: transform .6s ease-in-out, -webkit-transform .6s ease-in-out;
}


.carousel-inner{
max-height: 500px !important;
}
.margem{
  border: 1px solid black;

}

.margem-azul{
  border: 1px solid blue;

}

.cotaçao{
  display: flex;

  justify-content: space-around;

  align-items: center;
}

.cotaItem{
  display: flex;
  align-items: center;
}

.flag{
  width: 50px;
  height: 50px;
}

#agenda {
  text-shadow: 1px 1px 4px black;
  color: white;
  width: 100%;
  background-image: url(/static/media/AgendaBack.2b86815c.png);
  background-size: contain;
  background-repeat: no-repeat;
}
#agenda-titulo{

  font-size: 60px !important;

}
#agenda-items{
  display: flex;
  margin: auto;
  justify-content: center;

}

#caixa{
  display: flex;
  margin: 10px;
  flex-direction: column;
  width: 250px;
  height: 485px;

  background-image: url(/static/media/AgendaItem.0e2d46eb.png);
  background-size: cover;
  background-repeat: no-repeat;

  color: white;
  text-shadow: 1px 1px 4px black;
  text-align: center;
}


#dia{
  position: relative;
  top: 24px;
  left: -32px;
}

#mes{
  position: relative;
  top: -13px;
  left: 38px;
}

.diaMes{
  font-family: 'Merienda', cursive;
  font-size: 60px !important;
  max-height: 135px;
}

#localiza{
  font-size: 37px !important;
  margin-top: 13px;
}

#selectionPane{

  padding: 15px;

  border: 1px solid black;
  border-radius: 5px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);

}

.selectionTitle{  
  margin-bottom: 20px;
}
.selectionContent{
  display: flex;
}



.react-datepicker-popper{
  position: relative !important;
  translate: 0, 0 !important;

}

.dropbtn {
  background-color: #4CAF50;
  color: white;
  padding: 5px;
  font-size: 16px;
  border: none;
  cursor: pointer;

  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.3);

}

.dropbtn:hover{
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display: inline-block;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 130px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

/* Links inside the dropdown */
.dropdown-content button {

  color: black;
  background-color: #ffff;
  width: 100%;
  padding: 5px;
  font-size: 16px;
  border: none;

  text-decoration: none;
  display: block;
}

/* Change color of dropdown links on hover */
.dropdown-content button:hover {background-color: #f1f1f1}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block;
}

/* Change the background color of the dropdown button when the dropdown content is shown */
.dropdown:hover .dropbtn {
  background-color: #3e8e41;

  
}

.react-datepicker{
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);

}

#timeDate{
  min-width: 150px;
}

.flex-two{
  flex:2 1;
}

.flex{
  flex:1 1;
}

@media (max-width:480px)  {

  .App {
    text-align: center;
  }
  #nav-logo img{
    max-width: 100px;
  
  }
  .sliderImg{
    max-width: 480px;
  }
  .cotaçao{
    width: 100%;
  }
  .flag{
    width: 40px;
    
  }
  #page-body{
    margin-top: 68px;
  }
  #caixa{
    margin: 8px;
    width: 103px;
    height: 202px;
    
  }
  #agenda-titulo{

    font-size: 25px !important;
  
  }
  #agenda-items{
    display: flex;
    margin: auto;
    justify-content: center;
  
  }
  #agenda {
    width: 100%;
    
  }
    
  #dia{
    top: 15px;
    left: -13px;
  }

  #mes{
    top: 3px;
    left: 16px;
  }

  .diaMes{
    font-size: 23px !important;
  }

  #localiza{
    font-size: 18px !important;
    margin-top: 6px;
  }

  
}

